	module.exports = {"data":{"site":{"siteMetadata":{"title":"Casual Cognitions","author":"Andrew Usher"}},"markdownRemark":{"id":"/home/andrew/code/new-blog/src/pages/2018-02-21-what-do-i-use/index.md absPath of file >>> MarkdownRemark","html":"<p>This post details the things I use everyday, software or hardware. I’m looking forward to changing things up very soon, so I’ll make sure I update this post. If there’s anything I’m missing, you can let me know via any of my social media accounts and email.</p>\n<h3>Editor + Terminal</h3>\n<ul>\n<li>Visual Studio Code is my current editor which I have been using since December 2017. Before that, I was using Sublime Text.</li>\n<li>I switch between the Palenight and Cobalt2 themes.</li>\n<li>My font is currently Hack</li>\n<li>I switch between Git Bash and Hyper for my\nterminal</li>\n<li>I use Dev Tools Author for my Chrome Devtools</li>\n</ul>\n<h3>Desktop Apps</h3>\n<ul>\n<li>I use Text Expander for my canned responses and a majority of my code snippets.</li>\n<li>I use Trello and GTD for task management.</li>\n<li>I design in Figma and Adobe Illustrator.</li>\n<li>I do my writing in Caret.</li>\n<li>I use LiceCap to record.</li>\n<li>I just started using Polacode for code screenshots.</li>\n</ul>","frontmatter":{"title":"What Do I Use","date":"February 21, 2018"}}},"pathContext":{"slug":"/2018-02-21-what-do-i-use/","previous":{"fields":{"slug":"/2018-02-21-welcome-to-my-blog/"},"frontmatter":{"title":"Welcome to My Blog!"}},"next":{"fields":{"slug":"/2018-02-24-want-to-learn-faster/"},"frontmatter":{"title":"Want to Learn Faster? Double Your Rate of Failure"}}}}

